import gql from "graphql-tag"

export const MUTATE_ADD_CHAT_SESSION = gql`
	mutation MUTATE_ADD_CHAT_SESSION($receiver_id: Int!) {
		addChatRoom(receiver_id: $receiver_id) {
			text
			success
			chatParentId
		}
	}
`

export const QUERY_LIST_CHAT_ROOM = gql`
	query QUERY_LIST_CHAT_ROOM($roleUser: Int!) {
		listChatRoom(roleUser: $roleUser) {
			chatRoomPvid
			userPvid
			firstName
			lastName
			resource {
				path
			}
			createdAt
		}
	}
`

export const QUERY_LIST_CONVERSATION = gql`
	query QUERY_LIST_CONVERSATION($receiverId: Int!) {
		conversationList(receiverId: $receiverId) {
			chatRoomPvid
			conversations {
				userPvid
				name
				message
				createdAt
			}
		}
	}
`

export const MUTATE_ADD_CONVERSATION = gql`
	mutation MUTATE_ADD_CONVERSATION(
		$chatRoomPvid: Int!
		$message: String!
		$senderName: String!
		$isFirstTime: Boolean!
		$studyCenterPvid: Int
	) {
		addConversation(
			chatRoomPvid: $chatRoomPvid
			message: $message
			senderName: $senderName
			isFirstTime: $isFirstTime
			studyCenterPvid: $studyCenterPvid
		) {
			chatRoomPvid
			name
			message
			createdAt
			userPvid
		}
	}
`
