import React, { useEffect, useRef, useContext } from "react"
import { Section, Heading, Empty, Layout, Button } from "components"
import { Row, Col, Input, List, Avatar, Form, Popover } from "antd"
import { useQuery, useMutation } from "react-apollo"
import { QUERY_LIST_CHAT_ROOM, QUERY_LIST_CONVERSATION, MUTATE_ADD_CONVERSATION } from "queries/chat"
import { newClient } from "helpers"
import { useState } from "react"
import moment from "moment"
import styled from "styled-components/macro"
import { baseStyles } from "styles/base"
import Bubble from "components/common/Chat/Bubble"
import FadeIn from "react-fade-in"
import { SocketContext } from "App"
import { MUTATE_USER_PROFILE_HOME } from "queries/profile"
import { Formik } from "formik"
import TextInput from "components/forms/TextInput"
import { useLocation, useHistory } from "react-router"
import { Picker } from "emoji-mart"

import "emoji-mart/css/emoji-mart.css"
import { country } from "App"
import { SERVER_BASE_URL } from "helpers/constants"

const chatClient = newClient("chat")
const profileClient = newClient()

const StyledTextInput = styled(TextInput)`
	input.ant-input {
		height: 42px;
		border-radius: 8px;
		border: none;
	}
`

const ListItem = styled(List.Item)`
	cursor: pointer;
	.ant-list-item-meta {
		.ant-list-item-meta-content {
			text-align: left;
		}
	}
	.datetime {
		color: ${baseStyles.greyColor};
	}

	&:hover {
		background-color: ${baseStyles.primaryColorRgba};
	}
`

const SendInput = styled.div`
	position: sticky;
	bottom: 0;
	left: 0;
	.ant-form {
		padding: 2em 0;
		background-color: #f0f2f5;
		.ant-form-explain {
			display: none;
		}
	}
`

const ChatContentSection = styled.section`
	height: 80vh;
	overflow-y: scroll;
`

export default function Chats() {
	const [listChatRoom, setListChatRoom] = useState([])
	const [selectedChatRoom, setSelectedChatRoom] = useState({})
	const [conversationList, setConversationList] = useState([])
	const [isAddingMessage, setIsAddingMessage] = useState(false)
	const [searchValue, setSearchValue] = useState("")
	const [userData, setUserData] = useState({})
	const { state = {}, search } = useLocation()
	const { push } = useHistory()
	const token = localStorage.getItem("token")

	let userFromProfile = state.userData || {}

	const [fetchProfile] = useMutation(MUTATE_USER_PROFILE_HOME, {
		client: profileClient,
		onCompleted: (data) => {
			data = data.getProfile || {}
			setUserData(data)
		}
	})

	const { socket, onNewMessage } = useContext(SocketContext)

	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	const mePvid = Number(userDetails.pvid)

	const sectionRef = useRef(null)

	useQuery(QUERY_LIST_CHAT_ROOM, {
		client: chatClient,
		variables: { roleUser: Number((userDetails.role || {}).pvid) },
		onCompleted: (data) => {
			data = data.listChatRoom
			setListChatRoom(data)
		}
	})

	useQuery(QUERY_LIST_CONVERSATION, {
		client: chatClient,
		variables: { receiverId: /* Number(userFromProfile.pvid) || */ Number(selectedChatRoom.userPvid) },
		onCompleted: (data) => {
			data = data.conversationList || {}
			setConversationList(data.conversations)
			setSelectedChatRoom((prev) => ({ ...prev, chatRoomPvid: data.chatRoomPvid }))
		}
	})

	const [addConversation] = useMutation(MUTATE_ADD_CONVERSATION, {
		client: chatClient,
		onCompleted: (data) => {
			data = data.addConversation || {}
			// setConversationList(prev => [...prev, { ...data, userPvid: mePvid, sender: "me" }].filter(item => !isNaN(item.userPvid)))
			socket.emit("new_message", {
				chatRoomPvid: Number(selectedChatRoom.chatRoomPvid),
				name: userData.firstName + " " + userData.lastName,
				message: data.message,
				userPvid: mePvid
			})
		}
	})

	const handleSearchChat = (value) => {
		const filtered = listChatRoom.filter(({ firstName, lastName }) => {
			const userName = (firstName + " " + lastName).toLowerCase()
			return userName === value
		})
		setSearchValue(value)
		setListChatRoom(filtered)
	}

	const handleAddConversation = (values, { setSubmitting, resetForm }) => {
		setIsAddingMessage(true)
		values = { ...values, chatRoomPvid: Number(selectedChatRoom.chatRoomPvid) }
		addConversation({ variables: values }).finally(() => {
			setSubmitting(false)
			resetForm({})
		})
	}

	const handleJoinChat = (item) => {
		setSelectedChatRoom(item)
		sectionRef.current && sectionRef.current.scrollTo(0, sectionRef.current.scrollHeight)
		socket.emit("join_chat_room", { chatRoomPvid: item.chatRoomPvid })
	}

	const handleKeyUp = (e) => {
		socket.emit("typing_in_chat_room", {
			chatRoomPvid: Number(selectedChatRoom.chatRoomPvid),
			firstName: userData.firstName,
			lastName: userData.lastName
		})
	}

	useEffect(() => {
		if (!token) {
			push(`/${country}/login`)
		}

		if (sectionRef.current && selectedChatRoom.firstName) {
			sectionRef.current.scrollTo(0, sectionRef.current.scrollHeight)
		}
	}, [push, selectedChatRoom.firstName, token])

	useEffect(() => {
		if (Object.keys(userFromProfile).length > 0) {
			setSelectedChatRoom({
				userPvid: userFromProfile.pvid,
				firstName: userFromProfile.firstName,
				lastName: userFromProfile.lastName,
				resource: { path: userFromProfile.avatar[userFromProfile.avatar.length - 1].path }
			})
		}

		socket.on("new_message", (message) => {
			console.log({ newMessage: message })
			setConversationList((prev) => [
				...prev,
				{ ...message, chatRoomPvid: Number(message.chatRoomPvid), userPvid: Number(message.userPvid) }
			])
			sectionRef.current && sectionRef.current.scrollTo(0, sectionRef.current.scrollHeight)
		})

		fetchProfile()
	}, [socket])

	// console.log({ listChatRoom, conversationList, userFromProfile, selectedChatRoom, userData })

	return (
		<Layout>
			<Section className="p2em">
				<Row gutter={64} type="flex" justify="center">
					<Col lg={6} className="ta-center border-right">
						<Input.Search
							value={searchValue}
							onChange={handleSearchChat}
							name="search"
							placeholder="Search the chats..."
							className="mb2em"
						/>
						<List
							itemLayout="horizontal"
							dataSource={listChatRoom}
							locale={{
								emptyText: <Empty description="No chat found" />
							}}
							renderItem={(item) => (
								<ListItem onClick={() => handleJoinChat(item)}>
									<List.Item.Meta
										title={item.firstName + " " + item.lastName}
										avatar={<Avatar src={SERVER_BASE_URL + item.resource.path} />}
									/>
									<div className="datetime">{moment(item.createdAt).fromNow()}</div>
								</ListItem>
							)}
						/>
					</Col>
					<Col lg={14}>
						<Row type="flex" align="middle" className="mb1em">
							{selectedChatRoom.firstName && (
								<Col lg={2}>
									<Avatar size="large" src={SERVER_BASE_URL + (selectedChatRoom.resource || {}).path} />
								</Col>
							)}
							<Col lg={10}>
								<Heading
									level={4}
									marginBottom="0"
									content={
										!selectedChatRoom.firstName
											? "Select person to chat"
											: selectedChatRoom.firstName + " " + selectedChatRoom.lastName
									} /* subheader="Sun, 21 Jan 2020" */
								/>
							</Col>
						</Row>
						{/* {selectedChatRoom.chatRoomPvid ? ( */}
						<ChatContentSection ref={sectionRef}>
							{conversationList.length === 0 ? (
								<Empty
									description={
										selectedChatRoom.firstName ? (
											<span>
												Start chatting with <strong>{selectedChatRoom.firstName}</strong>
											</span>
										) : (
											"No chat found"
										)
									}
								/>
							) : (
								conversationList.map((item) => (
									<FadeIn key={item.createdAt}>
										<Bubble
											key={item.createdAt}
											isChatPage={true}
											data={{ ...item, time: item.createdAt, sender: item.userPvid === mePvid ? "me" : "them" }}
										/>
									</FadeIn>
								))
							)}
						</ChatContentSection>
						{/* // ) : (
						// 	<Loading />
						// )} */}
						{selectedChatRoom.firstName && (
							<SendInput>
								<Formik
									initialValues={{
										message: "",
										senderName: userData.firstName + " " + userData.lastName,
										sender: "me",
										isFirstTime: false
									}}
									onSubmit={handleAddConversation}
								>
									{({ handleSubmit, setFieldValue, values }) => (
										<Form layout="vertical" onSubmit={handleSubmit}>
											<Row gutter={16}>
												<Col lg={20}>
													<StyledTextInput
														name="message"
														placeholder="Type your message..."
														mb="0"
														onKeyUp={handleKeyUp}
													/>
												</Col>
												<Col lg={4}>
													<Popover
														title="Emoji"
														trigger="click"
														content={
															<Picker
																set="apple"
																onSelect={(emoji) =>
																	setFieldValue("message", values.message + emoji.native)
																}
															/>
														}
													>
														<Button type="default">
															<span role="img" aria-label="Smile">
																ðŸ˜„
															</span>
														</Button>
													</Popover>
												</Col>
											</Row>
										</Form>
									)}
								</Formik>
							</SendInput>
						)}
					</Col>
				</Row>
			</Section>
		</Layout>
	)
}
