import React from "react"
import Card from "components/Card"
import styled from "styled-components"
import { Row, Col, Typography } from "antd"
import moment from "moment"
import { baseStyles } from "styles/base"

const StyledBubble = styled(Row)`
	.ant-card {
		margin-bottom: 1em;
		max-width: ${({ isChatPage }) => (isChatPage ? "85%" : "75%")};
		float: ${({ sender }) => (sender === "me" ? "right" : "left")};
		box-shadow: none;
		/* box-shadow: ${baseStyles.boxShadow.hover}; */
		.ant-card-body {
			margin-bottom: 0;
			padding: 1em;
			font-size: 1em;
			border-radius: 8px;
			background: ${({ sender }) => (sender === "me" ? "rgba(0, 55, 255, 0.16)" : baseStyles.lightGrey.one)};
		}
	}
`

export default function Bubble({ data, isChatPage }) {
	const { sender } = data
	const dateTime =
		moment(data.time).format("DD MM YYYY") === moment().format("DD MM YYYY")
			? moment().format("HH:mm")
			: moment(data.time).format("DD MMM - HH:mm")

	// console.log({ time: moment(data.time).format("DD MM YYYY"), momentToday: moment().format("DD MM YYYY") })

	return (
		<StyledBubble sender={sender} isChatPage={isChatPage}>
			<Col lg={24}>
				<Card autoHeight noHover className={sender === "me" ? "ta-right" : "ta-left"}>
					<Typography.Paragraph>{data.message}</Typography.Paragraph>
					<Typography.Text className="light-grey">{dateTime}</Typography.Text>
				</Card>
			</Col>
		</StyledBubble>
	)
}
